.table {
  border-collapse: collapse;
  width: 100%;
}

.table-wrapper {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.table-header {
  background-color: var(--something);
  color: var(--background-color);
  position: sticky;
  top: 0;
}
.table-body {
  width: 100%;
}

.table-row:hover {
  background-color: var(--something-lighter);
}